import React from "react";

import { Link } from "gatsby";

import Button from "./button";

const Pagination = ({
  section,
  pagesQuantity,
  currentPage,
  className,
}: {
  section: "projects" | "blog";
  pagesQuantity: number;
  currentPage: number;
  className?: string;
}) => {
  const pages = Array.from({ length: pagesQuantity }, (_, i) => i + 1);
  const nextPage = currentPage + 1;

  return (
    <div className={`my-8 grid grid-cols-3 w-full ${className}`}>
      <div className="col-start-1 col-span-2 lg:col-start-2 lg:col-span-1 w-full flex justify-center">
        {currentPage < pagesQuantity ? (
          <Link to={`/${section}/page/${nextPage}`}>
            <Button size="large" variant="dark" className="w-max">
              Next page
            </Button>
          </Link>
        ) : null}
      </div>
      <nav className="col-start-3 col-span-1 w-full flex justify-center">
        {pagesQuantity > 1
          ? pages.map((page) => (
              <Link
                key={`pagination-link-${page}`}
                to={`/${section}/page/${page}`}
              >
                <Button
                  size="small"
                  variant={page === currentPage ? "dark" : "light"}
                  border={page !== currentPage}
                  className="mx-[-1px]"
                >
                  {page}
                </Button>
              </Link>
            ))
          : null}
      </nav>
    </div>
  );
};

export default Pagination;
