import React from "react";

import { Link } from "gatsby";

import { IPostPreviewQueryResult } from "../types/post.type";
import { LocalBusinessMetadata } from "./metadata";

import Quote from "../assets/common/chat-right-quote.svg";

const PostPreview = ({
  post,
  className,
  fontColor,
}: {
  post: IPostPreviewQueryResult;
  className?: string;
  fontColor: "main" | "secondary";
}) => {
  const { date, title, text } = post.frontmatter;

  const fontColorClass100 =
    fontColor === "main" ? "text-main-100" : "text-secondary-100";
  const fontColorClass10 =
    fontColor === "main" ? "text-main-10" : "text-secondary-10";
  const fontColorHover =
    fontColor === "main" ? "hover:text-main-100" : "hover:text-secondary-100";

  return (
    <Link
      to={`/blog/${post.id}`}
      className={`flex w-full ${fontColorClass10} ${fontColorHover} ${className}`}
      itemScope
      itemType="https://schema.org/Article"
    >
      <div
        className="hidden md:flex md:basis-1/3 shrink-0 justify-center p-6"
        itemProp="image"
      >
        <Quote className={fontColor === "secondary" ? "" : "brightness-0"} />
      </div>
      <div className=" pl-12 flex flex-col basis-1/2 md:basis-1/3 shrink-0">
        <p className={`text-3xl font-light ${fontColorClass10} pb-5`}>{date}</p>
        <h3
          className={`text-4xl font-bold ${fontColorClass100}`}
          itemProp="headline"
        >
          {title}
        </h3>
      </div>
      <div className="flex flex-col basis-1/2 md:basis-1/3 shrink-0">
        <p
          className={`font-normal text-2xl leading-[1.375rem] ${fontColorClass10}`}
          itemProp="abstract"
        >
          {text.split(" ").slice(0, 40).join(" ")}...
        </p>
        <p className={`text-2xl font-bold pt-6`}>Read more</p>
      </div>
      <LocalBusinessMetadata itemProp="copyrightHolder" />
    </Link>
  );
};

export default PostPreview;
