import React from "react";

import { HeadFC, PageProps } from "gatsby";
import { graphql } from "gatsby";

import { IBlogPageData, IBlogPageContext } from "../types/blog.type";

import Banner from "../components/banner";
import ContentContainer from "../components/contentContainer";
import PostPreview from "../components/postPreview";
import Pagination from "../components/pagination";

const Blog = ({
  data,
  pageContext,
}: PageProps<IBlogPageData, IBlogPageContext>) => {
  const posts = data.allMarkdownRemark.nodes;
  const { currentPage, pagesQuantity } = pageContext;

  return (
    <>
      <Banner
        heightClass="h-96"
        title="CodeCave blog"
        text="Awesome blog provides important information for you"
      />
      <ContentContainer className="flex-col items-center p-12">
        {posts
          ? posts.map((post) => (
              <PostPreview
                key={post.id}
                post={post}
                fontColor="main"
                className="py-5 border-t-2 first:border-t-0 border-solid border-main-100"
              />
            ))
          : null}
        <Pagination
          section={"blog"}
          pagesQuantity={pagesQuantity}
          currentPage={currentPage}
        />
      </ContentContainer>
    </>
  );
};

export default Blog;

export const Head: HeadFC = () => <title>CodeCave Blog</title>;

export const query = graphql`
  query ($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "posts" } } }
      sort: { frontmatter: { date: DESC } }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "MMM DD, YYYY")
          author
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
      }
    }
  }
`;
